import React, { useEffect,useState} from 'react';
import axios from 'axios';
import moment from 'moment';
function Showdate(props) {
  const [data1,setdata1]=useState([]);
  const [data2,setdata2]=useState([]);
  const [Mtotal,setMtotal]=useState([]);
  const [Etotal,setEtotal]=useState([]);
 const[startDate,setStartDate]=useState(new Date("2024-01-03"));
 const[endDate,setEndDate]=useState(new Date("2024-10-03"));
  
 const updateDates = (start, end) => {
  setStartDate(start);
  setEndDate(end);
};
  function ShowingEntries({ item }) {
    return (
      <tbody>
          <tr className="MorningData">
            <td>{moment(item.date, 'YYYY-MM-DD').format('DD-MMM-YYYY')}</td>
            <td>{item.weight}</td>
            <td>{item.fat}</td>
            <td>{item.price}</td>
            <td>{item.total}</td>
          </tr>
        </tbody>
    );
  }

  function showdata(data,Mtotal1){
   return <table className="morningEntries">
    <thead>
      <tr className="MorningHeading">
        <th>DATE</th>
        <th>WEIGHT</th>
        <th>FAT</th>
        <th>PRICE</th>
        <th>TOTAL PRICE</th>
      </tr>
    </thead>
    {data.length > 0 && data.map(item => (
<ShowingEntries key={item.id} item={item} /> ))}
<tr className="MorningHeading" style={{ color: 'black' }}>
        <th>{Mtotal1.totaldate} DAYS</th>
        <th>{Mtotal1.totalweight} KG</th>
        <th>----</th>
        <th>----</th>
        <th>{Mtotal1.totalmoney} RS</th>
      </tr>

  </table>

  }
  const fetchData = async () => {
    try {
      const response = await axios.post('/showEntries',{startDate,endDate});
      
      if (!response.data) {
        throw new Error('No data received');
      }
      const { morningEntries, eveningEntries,morningTotal,eveningTotal} = response.data;
      setdata1(morningEntries);
      setdata2(eveningEntries); 
      setMtotal(morningTotal);
      setEtotal(eveningTotal);
    } catch (error) {
      console.error('There was a problem with the fetch operation:', error);
    }
  };

  useEffect(() => {
    fetchData();
  }, []); // Empty dependency array to run effect only once

  return <div className='ENTRIES'>
  <div><h1>MORNING</h1>
  {console.log(Mtotal)}
  {console.log(Etotal)}
   {showdata(data1,Mtotal)}</div>
   <div><h1>EVENING</h1>
   {showdata(data2,Etotal)}
   </div></div>
}

export default Showdate;