import React, { useState } from "react";
import Showdate from "./showDate";
import Sidebar from "./sidebar";
import Entry from "./entry";

function Main() {
  const [page, setPage] = useState("");

  function settingPage(data) {
    setPage(data);
  }

  return (
    <>
      <Sidebar settingPage={settingPage} />
      <div className="main">
      {page === "" && <h1>HELLO BAHILOG</h1>}
      {page === "ENTRIES" && <Entry />}
        {page === "VIEW ENTRIES" && <Showdate />}
      </div>
    </>
  );
}

export default Main;
