import React from "react";

function Sidebar({ settingPage }) {
  return (
    <div className="sidebar">
      <div>
        <button onClick={() => settingPage("")}>HOME</button>
      </div>
      <div>
        <button onClick={() => settingPage("ENTRIES")}>ENTRIES</button>
      </div>
      <div>
        <button onClick={() => settingPage("VIEW ENTRIES")}>VIEW ENTRIES</button>
      </div>
      <div>
        <button onClick={() => settingPage("BALANCE")}>BALANCE</button>
      </div>
    </div>
  );
}

export default Sidebar;
