import React, { useEffect, useState } from 'react';
import axios from 'axios'; // Don't forget to import axios

// Move the show function inside the component
function Table({ datesBetween }) {
  const [work, setWork] = useState(datesBetween);

  useEffect(() => {
    setWork(datesBetween);
  }, [datesBetween]);

  async function handleClick(e, timeOfDay) {
    e.preventDefault();

    try {
      // Example POST request to the server
      const response = await axios.post(`/entries/${timeOfDay}`, {
        date: e.target.elements.date.value,
        weight: e.target.elements.weight.value,
        fat: e.target.elements.fat.value,
        price: e.target.elements.price.value
      });
      console.log(response.data);
    } catch (error) {
      // Handle error if request fails
      console.error('Error sending request:', error);
    }
  }

  function show(date, index, timeOfDay) {
    return (
      <tr key={index}>
        <td>
          <form onSubmit={(e) => handleClick(e, timeOfDay)}>
            <input name="date" value={date}></input> {/* Use lowercase 'value' */}
            <input name='weight' required></input>
            <input name='fat' required></input>
            <input name="price" value="75"/>
            <input name='submit' type='submit' value='Submit' />
          </form>
        </td>
      </tr>
    );
  }

  return (
    <div>
      {work.map((item, index) => (
        show(item, index, "morning")
      ))}
    </div>
  );
}

export default Table;
